import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Redirect } from "react-router-dom";

import SiteUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/companyMaster/site/actions";
import { Box } from "../../../App/app.style";

const redirectTo = '/dashboard/companies/sites'

class UpdateSite extends Component {
  // Get All data diawal
  componentDidMount() {
    const { editSite } = this.props;
    const { id } = this.props.match.params;
    editSite(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.site.error) {
      notification("error", this.props.site.error);
      this.props.clearError();
    }

    if (this.props.site.success && !this.props.site.loadingSubmit) {
      this.props.history.replace(redirectTo);
    }
  }

  handleSubmit = value => {
    const { updateSite } = this.props;
    const { id } = this.props.match.params;
    updateSite(id, value);
  };

  render() {
    // console.log(this.props.site);

    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="sites.update.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          {this.props.site.loading && !this.props.site.loadingSubmit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
              <SiteUpdateForm auth={this.props.auth} submit={this.handleSubmit} site={this.props.site} />
            )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { auth: state.auth, site: state.site };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateSite);
