import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Row, Col, Alert } from "antd";

//const { Option } = Select;
const FormItem = Form.Item;

class SiteUpdateForm extends Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    //console.log(this.props.site);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { site } = this.props;
    const { loading, loadingSubmit } = site;
    const data = site.site ? site.site : [];
    const used = data.is_used;
    const error = site.errorField ? site.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="sites.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : (
          ""
        )}
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="sites.add.subtitle.data" />
        </Divider> */}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <FormItem
              {...formItemLayout}
              label={<IntlMessages id="sites.add.code" />}
              validateStatus={
                error.site_code || getFieldError("site_code") ? "error" : null
              }
              help={
                error.site_code ? error.site_code : getFieldError("site_code")
              }
            >
              {getFieldDecorator("site_code", {
                initialValue: data.site_code,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 30,
                    message: <IntlMessages id="maxLength30" />
                  }
                ]
              })(
                <Input
                  disabled={used ? true : false}
                  style={{ textTransform: "uppercase" }}
                />
              )}
            </FormItem>

            <FormItem
              {...formItemLayout}
              label={<IntlMessages id="sites.add.name" />}
            >
              {getFieldDecorator("site_name", {
                initialValue: data.site_name,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input disabled={used ? true : false} />)}
            </FormItem>
          </div>
        </div>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading || loadingSubmit}
            disabled={used ? true : false}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedSiteUpdateForm = Form.create({ name: "update_site" })(
  SiteUpdateForm
);

export default WrappedSiteUpdateForm;
